ul.progress-bar {
    height: 250px;
    list-style: none;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow: hidden;
    margin-top: 15px;
    /* align-self: flex-start; */
    /* margin-right: 200px; */
}
ul.progress-bar::after {
    content: '';
    position: absolute;
    top: 0;
    left: 5px;
    background: #fbbd14;
    width: 1px;
    animation: lineConnectDots 6s forwards;
    /* height: 50vh; */
}
ul.progress-bar li {
    background: #fbbd14;
    border-radius: 10px;
    width: 12px;
    height: 12px;
    z-index: 1;
    position: relative;
}
ul.progress-bar li.stop ~ li {
    background: #fbbd14;
}



