#thinline {
    width: 1px;
    background-color: white;
    animation: growFullLine 8s forwards;
    position: relative;
    top: 0;
    align-self: flex-start;
    opacity: 0.5;
}

@keyframes growFullLine {
    0% {
        height: 0px;
    }
    100% {
        height: 260vh;
    }
}
